
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 16px;
}
nav a,
span a {
  color: #aaa;
  letter-spacing: 5px;
  text-decoration: none;
}
nav a:hover,
span a:hover {
  color: #4a6e78;
  -webkit-transition: .8s;
  transition: .8s;
  text-decoration: none;
}



